@import url('https://fonts.googleapis.com/css2?family=PT+Sans&display=swap');

* {
    padding: 0;
    font-family: 'PT Sans', sans-serif;
    margin: 0;
}

a {
    text-decoration: none;
    color: black;
}

nav {
    font-family: 'PT Sans', sans-serif;
    background-color: #fff;
    box-shadow: 0 2px 4px -2px rgb(175 175 175 / 50%);
    box-sizing: border-box;
    display: flex;
    /* position: fixed; */
    top: 0;
    width: 100%;
    justify-content: space-around;
    align-items: center;
}

nav .navlef {
    display: inline-flex;
    height: 100%;
    position: relative;
    text-align: center;
    white-space: nowrap;
    align-items: center;
}

nav .navlef .logo {
    margin: 5px 20px;
}

nav .navlef .opt {
    padding-left: 25px;
}

nav .navlef .opt span {
    padding: 0 20px;
}

nav .navlef .opt span a:hover {
    padding: 5px 0;
    border-bottom: 3px solid #fd5b5d;
    transition: 0.6s cubic-bezier(0.93, -0.97, 0.03, 1.69);
}

#home a {
    padding: 5px 0;
    border-bottom: 3px solid #fd5b5d;
}

nav .navrig {
    margin-right: 20px;
}

nav .navrig .profile {
    min-width: 135px;
    background-color: #fd5b5d;
    border: 0;
    border-radius: 4px;
    box-sizing: border-box;
    color: #fff;
    cursor: pointer;
    display: inline-block;
    font-size: 15px;
    font-weight: 500;
    letter-spacing: .25px;
    line-height: 20px;
    padding: 8px 24px;
    text-align: center;
    text-decoration: none;
}

nav .navlef .logo span,
.cancel-btn {
    display: none;
}

@media (max-width: 850px) {

  nav {
    justify-content: space-between;
  }

    span.material-symbols-outlined.cancel-btn {
        text-align: end;
        color: white;
        font-size: 40px;
    }

    nav .navlef .opt {
        display: none;
    }

    nav .navlef .opt {
        position: fixed;
        height: 100vh;
        width: 100%;
        max-width: 400px;
        left: -150%;
        top: 0px;
        z-index: 6;
        display: block;
        padding: 15px;
        text-align: center;
        background-color: #2d3133eb;
        transition: all 0.3s ease;
        display: flex;
        flex-direction: column;
    }

    nav .navlef .show {
        left: 0%;
        transition: 0.3s ease-out;
    }

    nav .opt span {
        margin-left: 0px;
        height: 70px;
    }

    nav span a {
        font-size: 23px;
    }

    .close {
        display: initial;
    }

    nav .navlef .logo span {
        display: initial;
        color: #fd5b5d;
        font-size: 50px;
        margin-right: 10px;
    }


    nav .navlef .opt a {
        color: white;
        ;
    }

}

@media (max-width: 500px) {
    nav .navlef .logo img {
        width: 80px;
    }

    nav .navrig .profile {
        min-width: 100px;
    }

    nav .navlef .logo span {
        font-size: 40px;
    }

}